[ng-reflect-router-link],
.btn {
  display: inline-block;
  border: none;
  outline: none;
  cursor: pointer;
  transition: ease-in-out 350ms;
  transition-property: background-color, color, border, opacity;
  /*
	&:hover {
		box-shadow: 0 10px 10px -5px rgba(0, 0, 0, 0.15);
	}
	*/ }
  a, a:active, a:focus, a:hover, a:visited,
  button,
  button:active,
  button:focus,
  button:hover,
  button:visited,
  [ng-reflect-router-link],
  [ng-reflect-router-link]:active,
  [ng-reflect-router-link]:focus,
  [ng-reflect-router-link]:hover,
  [ng-reflect-router-link]:visited,
  .btn,
  .btn:active,
  .btn:focus,
  .btn:hover,
  .btn:visited {
    color: inherit;
    text-decoration: none;
    outline: none; }
  a[disabled],
  button[disabled],
  [ng-reflect-router-link][disabled],
  .btn[disabled] {
    opacity: 0.5;
    pointer-events: none; }

.link {
  text-decoration: underline;
  margin: 0 5px; }

.btn--discover {
  white-space: nowrap; }
  .btn--discover, .btn--discover:active, .btn--discover:focus, .btn--discover:hover, .btn--discover:visited {
    color: var(--foreground); }
  .btn--discover > span {
    display: inline-block;
    position: relative; }
    .btn--discover > span:after {
      content: '';
      position: absolute;
      left: 0;
      bottom: -6px;
      width: 100%;
      height: 4px;
      background: var(--foreground);
      transform-origin: left center;
      transition: transform ease-out 350ms; }
  .btn--discover:hover > span:after {
    transform: scale(0, 1);
    background: var(--foreground); }
  .section--negative .btn--discover {
    color: var(--foreground-negative); }
    .section--negative .btn--discover, .section--negative .btn--discover:active, .section--negative .btn--discover:focus, .section--negative .btn--discover:hover, .section--negative .btn--discover:visited {
      color: var(--foreground-negative); }
    .section--negative .btn--discover > span:after {
      background: var(--foreground-negative); }
    .section--negative .btn--discover:hover > span:after {
      background: var(--foreground-negative); }

.btn--link {
  display: inline-flex;
  align-items: center;
  font-size: 16px;
  font-weight: 500; }
  .btn--link span {
    display: inline-block;
    transition: all ease-in-out 350ms; }
  .btn--link .icon--arrow-left,
  .btn--link .icon--arrow-right {
    width: 8px;
    height: 13px; }
  .btn--link .icon--arrow-left {
    margin-right: 17px; }
  .btn--link .icon--arrow-right {
    margin-left: 17px; }
  .btn--link, .btn--link:active, .btn--link:focus, .btn--link:hover, .btn--link:visited {
    color: var(--foreground); }
  .btn--link:hover span {
    transform: translateX(10px); }

.btn--back {
  display: inline-flex;
  align-items: center;
  font-size: 16px;
  font-weight: 500; }
  .btn--back span {
    display: inline-block;
    opacity: 0.5;
    transition: all ease-in-out 350ms; }
  .btn--back .icon--arrow-left {
    width: 8px;
    height: 13px;
    margin-right: 10px;
    opacity: 0.5; }
  .btn--back, .btn--back:active, .btn--back:focus, .btn--back:hover, .btn--back:visited {
    color: var(--foreground); }
  .btn--back:hover span {
    transform: translateX(10px); }

.btn--download {
  font-size: 0; }
  .btn--download .icon--download,
  .btn--download .icon--issuu {
    width: 35px;
    height: 35px;
    transition: transform 350ms ease-out; }
  .btn--download:hover svg {
    transform: scale(1.1); }

.btn--nav {
  white-space: nowrap; }
  .btn--nav, .btn--nav:active, .btn--nav:focus, .btn--nav:hover, .btn--nav:visited {
    color: var(--foreground); }
  .btn--nav > span {
    display: inline-block;
    position: relative;
    font-size: 14px;
    font-weight: var(--font-extralight); }
    .btn--nav > span:after {
      content: '';
      position: absolute;
      left: 0;
      bottom: -1px;
      width: 100%;
      height: 1px;
      background: var(--foreground);
      transform: scale(0, 1);
      transform-origin: left center;
      transition: transform ease-out 350ms; }
  .btn--nav:hover > span:after {
    transform: scale(1, 1);
    background: var(--foreground); }
  .section--negative .btn--nav {
    color: var(--foreground-negative); }
    .section--negative .btn--nav, .section--negative .btn--nav:active, .section--negative .btn--nav:focus, .section--negative .btn--nav:hover, .section--negative .btn--nav:visited {
      color: var(--foreground-negative); }
    .section--negative .btn--nav > span:after {
      background: var(--foreground-negative); }
    .section--negative .btn--nav:hover > span:after {
      background: var(--foreground-negative); }

.btn--gallery {
  position: relative; }
  .btn--gallery .picture:before {
    content: '+';
    display: flex;
    justify-content: center;
    align-items: center;
    position: absolute;
    left: 0;
    bottom: 0;
    width: 35px;
    height: 35px;
    z-index: 1;
    font-size: 20px;
    background: var(--foreground);
    color: var(--foreground-negative); }

.btn--busy {
  position: relative;
  user-select: none;
  pointer-events: none;
  transition: none; }
  .btn--busy, .btn--busy:hover, .btn--busy:focus, .btn--busy:active, .btn--busy.active {
    color: rgba(0, 0, 0, 0) !important; }
  .btn--busy:before {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    content: '';
    display: block;
    width: 4px;
    height: 4px;
    border-radius: 50%;
    background: #fff;
    z-index: 2;
    margin-top: 4px;
    animation: ball 0.45s cubic-bezier(0, 0, 0.15, 1) alternate infinite;
    /*
	background: $primary;
	.section--negative & {
		background: $foreground-negative;
	}
	*/
    animation-delay: .15s; }
  .btn--busy > svg {
    display: none; }
  .btn--busy > span::before {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    content: '';
    display: block;
    width: 4px;
    height: 4px;
    border-radius: 50%;
    background: #fff;
    z-index: 2;
    margin-top: 4px;
    animation: ball 0.45s cubic-bezier(0, 0, 0.15, 1) alternate infinite;
    /*
	background: $primary;
	.section--negative & {
		background: $foreground-negative;
	}
	*/
    margin-left: -10px; }
  .btn--busy > span::after {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    content: '';
    display: block;
    width: 4px;
    height: 4px;
    border-radius: 50%;
    background: #fff;
    z-index: 2;
    margin-top: 4px;
    animation: ball 0.45s cubic-bezier(0, 0, 0.15, 1) alternate infinite;
    /*
	background: $primary;
	.section--negative & {
		background: $foreground-negative;
	}
	*/
    margin-left: 10px;
    animation-delay: .3s; }

